import React from 'react';

export const SectionWithImageButton = ({ title, description, img, buttonName }) => {
    return (
        <React.Fragment>
            <div className="work-info-banner m-t-100 section-with-image-button">
                <span className="banner-top-clip">Freelance</span>
                <h1>{title}</h1>
                <p>{description}</p>
                <img className="work-freelance-img" src={img} />
                <button className="welcome-greetings-hire-me m-t-middle" onClick={() => window.location.href = "mailto:inscyo@gmail.com"}>{buttonName}</button>
            </div>
        </React.Fragment>
    )
}

export const SectionDualDivisionsSkillList = ({ title, description, v1title, v2title, v1list, v2list }) => {
    return (
        <React.Fragment>
            <div className="work-info-banner m-t-50 ">
                <span className="banner-top-clip">Skills</span>
                <h1>{title}</h1>
                <p>{description}</p>
                <section className="work-info-banner-contain section-width-responsive">
                    <div className="banner-list">
                        <div className="banner-list-f">
                            <h2>{v1title}</h2>
                            {
                                v1list.map((list, index) => {
                                    return (
                                        <React.Fragment key={index}>
                                            <p><span className="material-icons">check_circle</span>{list}</p>
                                        </React.Fragment>
                                    )
                                })
                            }
                        </div>
                        <div className="banner-list-s">
                            <h2>{v2title}</h2>
                            {
                                v2list.map((list, index) => {
                                    return (
                                        <React.Fragment key={index}>
                                            <p><span className="material-icons">check_circle</span>{list}</p>
                                        </React.Fragment>
                                    )
                                })
                            }
                        </div>
                    </div>
                </section>
            </div>
        </React.Fragment>
    )
}

export const SectionLikeWelcomeBanner = ({ title, titlebt, description, descriptionbt, img }) => {
    return (
        <React.Fragment>
            <div className="welcome-background">
                <section className="welcome-section section-width-responsive">
                    <div className="welcome-greetings">
                        <h1>{title} <br /> {titlebt}</h1>
                        <p>{description} <br />{descriptionbt}</p>
                        <ul className="social-media">
                            <li><a target="_blank" href="https://github.com/inscyo"><i className="fab fa-github-alt"></i></a></li>
                            <li><a target="_blank" href="mailto:inscyo@gmail.com"><i className="fas fa-envelope"></i></a></li>
                            <li><a target="_blank" href="https://facebook.com/jwenson"><i className="fab fa-facebook-messenger"></i></a></li>
                        </ul>
                    </div>
                    <div className="welcome-banner">
                        <img className="contact-greeting-img" src={img} />
                    </div>
                </section>
            </div>
        </React.Fragment>
    )
}