import React from 'react';

const Nav = () => {
    return (
        <React.Fragment>
            <nav className="navbar">
                <div className="navbar-container section-width-responsive">
                    <div className="navbar-logo">
                        <i className="fab fa-codepen"></i>
                    </div>
                    <div className="navbar-links">
                        <ul className="navbar-links-ul">
                            <li><a target="_blank" href="https://github.com/inscyo">Github</a></li>
                            <li><a target="_blank" href="https://facebook.com/jwenson">Facebook</a></li>
                            <li><a target="_blank" href="https://inscyo.github.io/portfolio-v1.1/">Works</a></li>
                            <li><a onClick={() => { alert("No data !"); }}>About</a></li>
                        </ul>
                    </div>
                    <div className="navbar-button-links">
                        <button className="lets-talk" type="button" onClick={() => window.open("https://inscyo.github.io/portfolio-v1.1/", "_blank")}>ver 1.1</button>
                        <button className="mobile-menu-toggle" type="button"><span className="material-icons">menu</span></button>
                    </div>
                </div>
            </nav>
        </React.Fragment>
    )
}

export default Nav;
