import React from 'react';

const Project = ({ title, titlevanish, description, projectList }) => {
    const openDemo = (type, demo, repo) => {
        switch (type) {
            case "demo":
                if (demo !== "") {
                    window.open(demo, "_blank")
                }
                break;
            case "repo":
                if (repo !== "") {
                    window.open(repo, "_blank")
                }
                break;
        }
    }
    return (
        <React.Fragment>
            <div className="work-info-banner m-t-100 ">
                <span className="banner-top-clip">Works</span>
                <h1 className="github-repo-title"><span className="hide-this-text-res">{titlevanish} </span>{title}<br /> </h1>
                <div className="work-project-card-contain">
                    <div className="work-project-cards">
                        {
                            projectList.map((list, index) => {
                                return (
                                    <React.Fragment key={index}>
                                        <div className="work-info-cards">
                                            <i className="fab fa-github"></i>
                                            <div className="work-info-details">
                                                <div className="work-info-details-desc" onClick={() => openDemo("demo", list.demo, "")}>
                                                    <p>{list.title}</p>
                                                    <span>{list.technology}</span>
                                                </div>
                                                <div className="work-info-btn">
                                                    <span className="material-icons" onClick={() => openDemo("demo", list.demo, "")}>public</span>
                                                    <span className="material-icons" onClick={() => openDemo("repo", "", list.repo)}>share</span>
                                                </div>
                                            </div>
                                        </div>
                                    </React.Fragment>
                                )
                            })
                        }
                    </div>
                </div>
                <button className="welcome-greetings-hire-me m-t-middle" onClick={() => window.open("https://inscyo.github.io/portfolio-v1.1/", "_blank")}>Show More</button>
            </div>

        </React.Fragment>
    )
}

export default Project
